
.home-page {
    margin-top: 50px;
    text-align: center;
  }
  
  .home-page h2 {
    margin-bottom: 20px;
  }
  
  .home-page button {
    background-color: #dc3545;
    color: #fff;
    border: none;
    padding: 10px 20px;
    font-size: 16px;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .home-page ul {
    list-style: none;
    padding: 0;
  }
  
  .home-page li {
    margin-bottom: 10px;
  }


  .home-page table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 20px;
  }
  
  .home-page th,
  .home-page td {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
  .home-page th {
    background-color: #f2f2f2;
    font-weight: bold;
  }
  
  .home-page tr:hover {
    background-color: #f2f2f2;
  }
  
  .home-page th:first-child,
  .home-page td:first-child {
    text-align: center;
  }
  
  .home-page td:nth-child(2) {
    padding-left: 20px;
  }
  
  @media (max-width: 768px) {
    .home-page table {
      width: 100%;
    }
  }
