.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.login-form {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-top: 50px;
  height: auto;
}

.login-form h2 {
  margin-bottom: 20px;
}

.login-form input {
  width: 90%;
  margin-bottom: 10px;
  height: auto;
  padding: 10px;
}

.login-form button {
  width: 100%;
  padding: 10px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
@media (max-width: 768px) {
  .login-form {
    max-width: 300px;
  }
}
